import React from "react"
import Blog from "../components/blog"
import BlogVideo from "../components/blog_video"
import BlogLink from "../components/blog_link"

import Container from 'react-bootstrap/Container'
import RealNavbar from "../components/real_navbar"

export default function Home() {
  return (
    <div>
      <link
        rel="stylesheet"
        href="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css"
        integrity="sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk"
        crossorigin="anonymous"
      />

      <RealNavbar/>
      <br/>
      <Container fluid>
        <br/>
        <BlogLink date="7/30/2022" title="SVO Ray Tracer Updates #7 - Explosion Effect" body="I've added an explosion effect to the voxel renderer. Read implementation details in my latest blog post!" 
                                                                            linktext="Read More"
                                                                            link="/svo_update7"/>
        <br/>

        <BlogLink date="7/28/2022" title="SVO Ray Tracer Updates 4, 5, and 6!" body="I've wrote three updates since I posted the last one. They're all posted now! I created an index to consolidate all current and future updates." 
                                                                            linktext="View SVO Ray Tracer Index"
                                                                            link="/svo_index"/>
        <br/>

        <BlogLink date="7/6/2022" title="SVO Ray Tracer Update #3" body="Improvements have been made to ray traversal and constant buffer limits were defeated! Learn more in the latest update..." 
                                                                            linktext="Read More"
                                                                            link="/svo_update3"/>
        <br/>
        
        <BlogLink date="6/9/2022" title="SVO Ray Tracer Update #2" body="This week I wrote the SVO data structure, uploaded it to the GPU, and began the traversal algorithm! I also created a demo with some fun colors and lighting to help visualize the algorithm." 
                                                                            linktext="Read More"
                                                                            link="/svo_update2"/>
        <br/>

        <BlogLink date="6/3/2022" title="SVO Ray Tracer Update #1" body="I'm working on a real-time voxel ray tracer using sparse voxel octrees! I just released
                                                                                     my first blog post - setting up the project and implementing ray-box intersection." 
                                                                            linktext="Read More"
                                                                            link="/svo_update1"/>
        <br/>

        <BlogLink date="5/22/2021" title="Creating a Snow Post Process Effect" body="This blog will be dedicated to breaking down the technical details of the snowy weather post-process effect in my upcoming puzzle game. 
                                                                            I implemented this within 
                                                                            my custom engine, but it could be done in any engine that supports custom shaders and post processing..." 
                                                                            linktext="Read More"
                                                                            link="/snow_effect"/>
        <br/>

        <BlogVideo date="4/27/2021" title="'Portal' System Demo" body="I've had my nose to the grindstone and am making fast progress on the engine. I recently added a system that will load new maps when the player enters a specific tile.
                                                                        One application of this is for entering/exiting buildings, where the interior and exterior are separate map files. I put together a short video to demonstrate this. Enjoy!" video="https://www.youtube.com/embed/aWF_azFEgNI" />
        <br/>

        <BlogVideo date="4/24/2021" title="NATURAL Engine Map Editor" body="I've been working on a tile-based 2D engine using C++, OpenGL, and SDL2 (for input/audio). For now, I'm calling it NATURAL Engine. This is a short demo
                                                                          of the engine's map editor. Not shown in this video is its ability to load and save maps in a binary format." video="https://www.youtube.com/embed/rV9T1dKSBs0" />
        <br/>

      </Container>

      <br/>
    </div>
  )
}
