import React from "react"

import Card from "react-bootstrap/Card"


export default function BlogVideo(props){
    return (
        
        <Card style={{maxWidth: '1200px'}}>
          <Card.Header style={{background: "#EEEEEE"}}>{props.date}</Card.Header>    
          <Card.Body>
          <Card.Title>{props.title}</Card.Title>
            <iframe width="560" height="315" style={{paddingRight:"19px", width: "90%", height: "90vh", maxWidth: 560, maxHeight: 315}}src={props.video} frameborder="0" allowfullscreen></iframe>
            <Card.Text>{props.body}</Card.Text>
          </Card.Body>
        </Card>
        
    )
  }